<template>
  <v-app>
    <v-content>
      <!-- <sidebar v-show='show'></sidebar> -->
      <nav-bar v-show='show'></nav-bar><br><br>
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
import localForage from 'localforage'
import navbar from '@/components/useful/NavBar.vue'
import Sidebar from '@/components/useful/Sidebar.vue'
export default {
  name: 'Farm',
  components: {
    'nav-bar': navbar,
    'sidebar': Sidebar
  },
  data () {
    return {
      show: false
    }
  },
  methods: {
    update () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        if (value.token) {
          obj.show = true
        } else { obj.show = false }
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  mounted () {
    this.update()
  }
}
</script>
<style>
body {
  margin: 0;
}
main {
  text-align: center;
  margin-top: 5px;
}
</style>



