<template>
  <div>
    <v-container>
      <h3>Account Details: -</h3>
      <div class='mt-2 text-xs-center' v-for='(pro, i) in profile' :key='i'>
      <v-layout row wrap >
        <v-flex><b>Name :</b> {{ pro.username }}</v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex><b>Email :</b> {{ pro.email }}</v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex><b>Cell No. :</b> {{ pro.mobile }}</v-flex>
      </v-layout>
      <v-layout row wrap >
        <v-flex><b>Address :</b> {{ pro.address }}</v-flex>
      </v-layout>
    </div>
    </v-container>
  </div>
</template>
<script>
import navbar from '@/components/useful/NavBar.vue'
import localForage from 'localforage'

export default {
  components: {
    'nav-bar': navbar
  },
  data () {
    return {
      profile: []
    }
  },
  methods: {
    getData () {
      var obj = this
      obj.profile = []
      localForage.getItem('user').then(function (value) {
        obj.profile.push(value)
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.getData()
  }
}
</script>



