<template>
  <div>
<v-navigation-drawer
      v-model="drawer"
      fixed
      clipped
    >
    <v-subheader class="mt-3 grey--text text--darken-1">Select Farm</v-subheader>
    <hr>
      <v-list dense>
        <v-list-tile v-for="item in items" :key="item.text" @click="">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ item.text }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-subheader class="mt-3 grey--text text--darken-1">Profile</v-subheader>
        <hr>
        <v-list>
          <v-list-tile v-for="item in items2" :key="item.text" avatar @click="">
            <v-list-tile-avatar>
              <img :src="`https://randomuser.me/api/portraits/men/${item.picture}.jpg`" alt="">
            </v-list-tile-avatar>
            <v-list-tile-title v-text="item.text"></v-list-tile-title>
          </v-list-tile>
        </v-list>
        <v-list-tile class="mt-3" @click="">
          <v-list-tile-action>
            <v-icon color="grey darken-1">add_circle_outline</v-icon>
          </v-list-tile-action>
          <v-list-tile-title class="grey--text text--darken-1">Browse Channels</v-list-tile-title>
        </v-list-tile>
        <v-list-tile @click="">
          <v-list-tile-action>
            <v-icon color="grey darken-1">settings</v-icon>
          </v-list-tile-action>
          <v-list-tile-title class="grey--text text--darken-1">Version 1.0.0</v-list-tile-title>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar
      color="gray"
      dense
      fixed
      clipped-left
      app
      height=60
    >
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-icon class="mx-3">fab fa-youtube</v-icon>
      <!-- <v-toolbar-title class="mr-5 align-center">
        <span class="title">Farmhand</span>
      </v-toolbar-title> -->
      <v-spacer></v-spacer>
      <v-layout>
        <v-flex id="flex" class="text-xs-left">
          <img class="mx-auto" src="@/assets/logo2.png" alt="logo" id="logo" @click="home">
        </v-flex>
      </v-layout>
      <div class="text-xs-center mt-2" id="contact"><span @click="contact" id="iconcon" :class="color"><u>Contact</u></span>
        <font-awesome-icon class="ml-2 pt-2" :color="icolor" icon="cog" id="iconset" @click="setting"/>
      </div>
    </v-toolbar>
    <v-content>
      Hello
    </v-content>
  </div>
</template>

<script>
  import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
  import localForage from 'localforage'
  import axios from 'axios'
  export default {
    components: {
      FontAwesomeIcon
    },
    data: () => ({
      drawer: false,
      items: [
        { icon: 'trending_up', text: 'Farm 1' },
        { icon: 'subscriptions', text: 'Farm 2' },
        { icon: 'history', text: 'Farm 3' },
        { icon: 'featured_play_list', text: 'Farm 4' },
        { icon: 'watch_later', text: 'Farm 5' }
      ],
      items2: [
        { picture: 58, text: 'Profile' },
        { picture: 78, text: 'Logout' }
      ]
    }),
    props: {
      source: String
    },
    methods: {
      clickPump () {
        this.showIcon = false
        this.$router.push('/farm/pump')
        this.color = 'black--text'
        this.icolor = 'black'
        this.colorBtnP = 'white'
        this.imgSrc = '/assets/icons8-Wpump-filled-50.png'
        this.colorBtnW = 'black'
        this.colorBtnC = 'black'
        this.colorBtnG = 'black'
      },
      clickWater () {
        this.showIcon = true
        this.$router.push('/farm/water')
        this.color = 'black--text'
        this.icolor = 'black'
        this.imgSrc = '/assets/icons8-pump-filled-50.png'
        this.colorBtnW = 'white'
        this.colorBtnP = 'black'
        this.colorBtnC = 'black'
        this.colorBtnG = 'black'
      },
      clickCrop () {
        this.showIcon = true
        this.$router.push('/farm/crop')
        this.color = 'black--text'
        this.icolor = 'black'
        this.imgSrc = '/assets/icons8-pump-filled-50.png'
        this.colorBtnC = 'white'
        this.colorBtnW = 'black'
        this.colorBtnP = 'black'
        this.colorBtnG = 'black'
      },
      clickGrid () {
        this.showIcon = true
        this.$router.push('/farm/grid')
        this.color = 'black--text'
        this.icolor = 'black'
        this.imgSrc = '/assets/icons8-pump-filled-50.png'
        this.colorBtnG = 'white'
        this.colorBtnC = 'black'
        this.colorBtnW = 'black'
        this.colorBtnP = 'black'
      },
      contact () {
        this.colorBtnG = 'black'
        this.colorBtnC = 'black'
        this.colorBtnW = 'black'
        this.colorBtnP = 'black'
        this.$router.push('/farm/contact')
        if (this.color === 'black--text') {
          this.color = 'blue--text'
          this.icolor = 'black'
        } else {
          this.color = 'black--text'
        }
      },
      setting () {
        this.colorBtnG = 'black'
        this.colorBtnC = 'black'
        this.colorBtnW = 'black'
        this.colorBtnP = 'black'
        this.$router.push('/farm/settings')
        if (this.icolor === 'black') {
          this.icolor = 'blue'
          this.color = 'black--text'
        } else {
          console.log('black')
          this.icolor = 'black'
        }
      },
      home () {
        this.$router.push('/farm/home')
        this.color = 'black--text'
        this.icolor = 'black'
        this.colorBtnG = 'gray'
        this.colorBtnC = 'black'
        this.colorBtnW = 'black'
        this.colorBtnP = 'black'
      },
      select (event) {
        this.colorBtnG = 'black'
        this.colorBtnC = 'black'
        this.colorBtnW = 'black'
        this.colorBtnP = 'black'
        this.icolor = 'black'
        this.color = 'black--text'
        if (event === 'Logout') {
          var obj = this
          localForage.getItem('user').then(function (value) {
            axios({
              method: 'get',
              url: obj.$hostname + '/users/logout',
              headers: {
                deviseid: value.devise_id,
                token: value.token,
                email: value.email
              }
            }).then(response => {
              console.log(response.data)
            }).catch(error => {
              console.log(error)
            })
          }).catch(function (err) {
            console.log(err)
          })
          localForage.clear()
          this.$router.push('/')
        }
        if (event === 'Profile') {
          this.$router.push('/farm/profile')
        }
      }
    }
  }
</script>
<style scoped>
#icon{
  height: 35px;
  width: 35px;
}
#nav{
  top: 57px;
  background: linear-gradient(0.25turn, #90CAF9, #1565C0);
}
#contact{
  font-size: 18px;
}
#iconset{
  height: 25px;
  width: 25px;
  cursor: pointer;
}
#logo{
  height: 50px;
  width: 50px;
  cursor: pointer;
}
#iconcon{
  cursor: pointer;
}
.menuicon{
  font-size: 18px;
}
</style>



