<template>
  <div>
    <!-- Count : {{count}} -->
    <br>
    <!-- <v-dialog v-model="terminateDialog" persistent max-width="420">
      <v-card>
        <v-card-title class="headline">Are you sure to terminate this crop manually?</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="terminateDialog = false">No</v-btn>
          <v-btn color="green darken-1" flat @click="terminateCropConfirm">Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog> -->
    <v-dialog v-model="terminateDialog" persistent max-width="420">
      <v-card>
        <v-card-title class="headline">You have terminate the crop : {{cropName}}</v-card-title>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="terminateCropOk">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-expansion-panel focusable>
      <v-expansion-panel-content v-for="(crop, i) in crops" :key="i">
        <div slot="header" class="text-xs-center">
          <v-layout row wrap justify-space-around>
            <v-flex xs2>
              {{crop.crop.cropName}}
            </v-flex>
            <v-flex xs2>
              {{crop.crop.croptypeId === 1?'Seed':crop.crop.croptypeId ===2?'Sapling':''}}
              <!-- <span v-if="crop.crop.croptype">{{crop.crop.croptype.cropTypeName}}</span> -->
            </v-flex>
            <v-flex xs2>
              Field : {{crop.field.fieldName}}
            </v-flex>
            <v-flex xs2>
              {{crop.startDate}}
            </v-flex>
            <v-flex xs2>
              <span v-if="crop.newEndDate === null">{{crop.endDate}}</span><span v-else>{{crop.newEndDate}}</span>
            </v-flex>
          </v-layout>
        </div>
        <v-card class="text-xs-center">
          <v-card-text>
            <v-layout class="text-xs-right">
              <v-flex><v-btn color="error" small flat outline dark class="mb-2 white--text" @click="terminateCrop(crop)">Terminate Crop</v-btn></v-flex>
            </v-layout>
            <br>
            Expected Yield on <span v-if="crop.newEndDate === null">{{crop.endDate}}</span><span v-else>{{crop.newEndDate}}</span> {{crop.expectedYield}} {{crop.crop.yieldType}}<br>
            Market Price ( $ {{crop.price}} per kilo)
            <div style="border: 1px solid #212121; border-radius: 25px;">
              <v-layout row wrap v-for="
              (note, j) in crop.additionalcomment"  :key="j">
                <v-flex xs4 style="padding: 25px;">
                  <div>{{note.date}}</div>
                </v-flex>
                <v-flex xs7 style="padding: 25px;">
                  <div>{{note.comment}}</div>
                </v-flex>
                <v-flex xs1>
                  <font-awesome-icon icon="times" @click="remove(j, i)" />
                </v-flex>
              </v-layout>
              <v-layout row wrap v-for="(t, i) in ts"  :key="t.i">
                <v-flex xs3 style="padding: 25px;">
                  <v-menu offset-y>
                    <div slot="activator" style="padding: 5px; border: 1px solid black; border-radius: 25px;">Select a Date <font-awesome-icon icon="calendar-alt" id="icon"/> &nbsp {{date}}</div>
                    <v-date-picker v-model="date" color="indigo accent-3" type="date"></v-date-picker>
                  </v-menu>
                </v-flex>
                <v-flex xs9 style="padding: 5px;">
                  <v-text-field placeholder="Enter a Comment" required v-model="texts"></v-text-field>
                </v-flex>
              </v-layout>
              <v-layout row wrap>
                <v-flex xs12 class="mx-auto">
                  <v-btn color="grey darken-1" class="mb-2 white--text" @click="addMore(i)">Confirm</v-btn>
                </v-flex>
              </v-layout>
            </div>
          </v-card-text>
        </v-card>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </div>
</template>
<script>
import axios from 'axios'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import localForage from 'localforage'
var deviseid
var token
var email

export default {
  data () {
    return {
      crops: [],
      crops1: [],
      currentCrop: [],
      date: '',
      texts: '',
      ts: 1,
      storedata: [][{date: '', comment: ''}],
      terminateDialog: false,
      fieldCropId: '',
      cropName: '',
      count: 0
    }
  },
  components: {
    FontAwesomeIcon
  },
  methods: {
    terminateCrop (fc) {
      this.fieldCropId = fc.fieldCropId
      this.cropName = fc.crop.cropName
      this.terminateCropConfirm()
      this.terminateDialog = true
    },
    terminateCropOk () {
      this.terminateDialog = false
      this.onPageLoad()
    },
    terminateCropConfirm () {
      var obj = this
      console.log(obj.fieldCropId)
      axios({
        method: 'post',
        url: obj.$hostname + '/fieldcrop/terminatecrop',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'fieldCropId': obj.fieldCropId,
          'farmId': localStorage.getItem('farm')
        }
      }).then(response => {
        obj.crops1 = []
        obj.currentCrop = []
        var i
        obj.crops1 = response.data.data
        console.log(obj.crops1)
        for (i = 0; i < obj.crops1.length; i++) {
          if (obj.crops1[i].status === 2) {
            obj.currentCrop.push(obj.crops1[i])
          }
        }
        localForage.setItem('CurrentCrop', obj.currentCrop)
      }).catch(error => {
        console.log(error)
      })
    },
    remove (j, i) {
      this.crops[i].additionalcomment.splice(j, 1)
      localForage.setItem('CurrentCrop', this.crops)
    },
    onPageLoad () {
      console.log('onPageLoad')
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
      }).catch(function (err) {
        console.log(err)
      })
      localForage.getItem('CurrentCrop').then(function (value) {
        obj.crops = value
        obj.count = obj.crops.length
      }).catch(function (err) {
        console.log(err)
      })
    },
    addMore (i) {
      this.crops[i].additionalcomment.push({ date: this.date, comment: this.texts })
      localForage.setItem('CurrentCrop', this.crops)
      axios({
        method: 'post',
        url: this.$hostname + '/users/add_comment',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'fieldCropId': this.crops[i].fieldCropId,
          'date': this.date,
          'comment': this.texts
        }
      }).then(response => {
        this.date = ''
        this.texts = ''
        localForage.setItem('CurrentCrop', this.crops)
      }).catch(error => {
        console.log(error)
      })
    }
  },
  mounted () {
    this.onPageLoad()
  }
}
</script>



