import axios from 'axios'
import localForage from 'localforage'

var access
export default {
  data () {
    return {
      crop: [],
      cropNew: [],
      cropCurrent: [],
      cropHistory: [],
      cropTerminated: [],
      cropCancel: [],
      farmId: '',
      farmName: ''
    }
  },
  methods: {
    dateChange (newdate, days) {
      var date
      newdate.setDate(newdate.getDate() + days)
      var dd = newdate.getDate()
      var mm = newdate.getMonth() + 1
      var y = newdate.getFullYear()
      if (mm <= 9) {
        if (dd <= 9) {
          date = y + '-' + '0' + mm + '-' + '0' + dd
        } else {
          date = y + '-' + '0' + mm + '-' + dd
        }
      } else if (mm >= 10) {
        if (dd <= 9) {
          date = y + '-' + mm + '-' + '0' + dd
        } else {
          date = y + '-' + mm + '-' + dd
        }
      }
      return date
    },
    changeFarm () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldcrop/all?farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: value.devise_id,
            token: value.token,
            email: value.email
          }
        }).then(response => {
          obj.crop = []
          obj.cropNew = []
          obj.cropCurrent = []
          obj.cropHistory = []
          console.log(response)
          obj.crop = response.data.data
          var i = 0
          for (i = 0; i < obj.crop.length; i++) {
            if (obj.crop[i].status === 1) {
              obj.cropNew.push(obj.crop[i])
            }
            if (obj.crop[i].status === 2) {
              obj.cropCurrent.push(obj.crop[i])
            }
            if (obj.crop[i].status === 3) {
              obj.cropTerminated.push(obj.crop[i])
            }
            if (obj.crop[i].status === 4) {
              obj.cropHistory.push(obj.crop[i])
            }
            if (obj.crop[i].status === 9) {
              obj.cropCancel.push(obj.crop[i])
            }
          }
          localForage.setItem('NewCrop', obj.cropNew)
          localForage.setItem('CurrentCrop', obj.cropCurrent)
          localForage.setItem('CropTerminated', obj.cropTerminated)
          localForage.setItem('CropHistory', obj.cropHistory)
          localForage.setItem('CropCancel', obj.cropCancel)
        }).catch(error => {
          console.log(error)
        })
      }).catch(function (err) {
        console.log(err)
      })
    },
    token () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        access = value.token
        // console.log(access['access-token'])
        if (access) {} else {
          obj.$router.push('/')
        }
      }).catch(function (err) {
        console.log(err)
        obj.$router.push('/')
      })
    },
    tokenLogin () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        access = value.token
        // console.log(access)
        if (access) {
          obj.$router.push('/farm/home')
        } else {
          obj.$router.push('/')
        }
      }).catch(function (err) {
        console.log(err)
        obj.$router.push('/')
      })
    }
  }
}



