<template>
  <v-container class="mt-5">
    <br><br>
    <span v-for="(bl,i) in breakline" :key="i"><span v-html="bl"></span></span>
    <div id="myDiv">
      <vo-basic :data="chartData" ></vo-basic>
    </div>
  </v-container>
</template>

<script>
import { VoBasic } from 'vue-orgchart'
import axios from 'axios'
import localForage from 'localforage'
var deviseid
var token
var email
export default {
  components: { VoBasic },
  data () {
    return {
      pumps: [],
      fieldpumps: [],
      fieldvalves: [],
      fields: [],
      chartData: {},
      breakline: []
    }
  },
  methods: {
    orgChart () {
      var i
      var j
      var k
      var farmsetup = []
      var children = [] //           #Pump Info
      var children1 //          #Valve Info
      // var children2 = [] //          #Field Info
      // var pv = []
      var obj = this
      for (k = 0; k < obj.pumps.length; k++) {
        children1 = []
        j = 0
        for (i = 0; i < obj.fieldpumps.length; i++) {
          if (obj.fieldpumps[i].actuatorId === obj.pumps[k].actuatorId) {
            children1[j] = {'name': obj.fieldpumps[i].fieldName}
            j = j + 1
            obj.breakline.push('<br>')
          }
        }
        children[k] = {'name': obj.pumps[k].actuatorName, 'children': children1}
      }

      farmsetup = {'name': 'Farmer', 'children': children}
      obj.chartData = Object.assign({}, farmsetup)
    },
    onPageLoad () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldactuator/farmsetup',
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          // console.log(response.data)
          obj.pumps = response.data.pumps
          obj.fieldpumps = response.data.fieldpumps
          obj.fieldvalves = response.data.fieldvalves
          obj.fields = response.data.fields
          obj.orgChart()
        }).catch(error => { console.log(error) })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  created () {
    this.onPageLoad()
  }
}
</script>
<style>
#myDiv {
    -webkit-transform: rotateZ(-90deg); /* Safari */
    transform: rotateZ(-90deg); /* Standard syntax */
}
</style>



