<template>
  <div>
    <v-alert v-model="alert" dismissible type="success" width=100px>
      Thank you for your feedback.
    </v-alert>
    <v-alert v-model="alert1" dismissible type="success" width=100px>
      Your end date of irrigation schedule has been extended to {{endDate}}
    </v-alert>
    <br>
  <v-expansion-panel focusable>
    <v-expansion-panel-content v-for="(crop, i) in crops" :key="i">
      <div slot="header" class="text-xs-center">
        <v-layout>
          <v-flex xs2>
            {{crop.crop.cropName}}
          </v-flex>
          <v-flex xs2>
            {{crop.crop.croptypeId === 1?'Seed':crop.crop.croptypeId ===2?'Sapling':''}}
            <!-- <span v-if="crop.crop.croptype">{{crop.crop.croptype.cropTypeName}}</span> -->
          </v-flex>
          <v-flex xs2>
            {{crop.field.fieldName}}
          </v-flex>
          <v-flex xs2>
            {{crop.startDate}}
          </v-flex>
          <v-flex xs2>
            <span v-if="crop.newEndDate === null">{{crop.endDate}}</span><span v-else>{{crop.newEndDate}}</span>
            <v-dialog
            ref="dialogxyz"
            v-model="crop.modelEndDate"
            :return-value.sync="crop.endDate2"
            lazy
            full-width
            width="290px"
            >
            <div slot="activator" ><font-awesome-icon icon="calendar-alt" id="icon"/> </div>
            <!-- <v-date-picker v-model="endDate" :min="crop.endDate" :max="crop.extendEndDate" color="indigo accent-3"> -->
            <v-date-picker v-model="endDate" :min="crop.startDate >=new Date().toJSON().slice(0,10)?crop.startDate:new Date().toJSON().slice(0,10)" color="indigo accent-3">
              <v-btn flat color="primary" @click="extendEndDate(crop.fieldCropId,endDate), crop.fieldId = false">OK</v-btn>
            </v-date-picker>
            </v-dialog>
          </v-flex>
        </v-layout>
      </div>
      <v-card class="text-xs-center">
        <v-layout row>
          <v-flex>
            Feedback for Irrigation
          </v-flex>
        </v-layout>
        <v-layout>
        <v-card-text>
          <v-slider
            v-model="crop.feedback"
            :tick-labels="ticksLabels"
            :max="6"
            step="1"
            ticks="always"
            tick-size="2"
          ></v-slider>
        </v-card-text>
        </v-layout>
        <v-layout>
          <v-flex> Irrigation in % {{ticksLabels[crop.feedback]}}</v-flex>
        </v-layout>
        <v-layout>
          <v-flex><v-btn class='submitField' small @click="pushFeedback(i,ticksLabels[crop.feedback])">Confirm</v-btn> </v-flex>
        </v-layout>
        </v-card>
        <v-card>
          <div class="text-xs-right">
              <v-menu offset-y>
              <v-btn slot="activator" color="primary" dark>Time Scale</v-btn>
              <v-list>
                <v-list-tile v-for="(item, index) in items" :key="index" @click="chart(item.title)">
                  <v-list-tile-title>{{ item.title }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </div>
          <v-card-text>
            <GChart type="ColumnChart"
            :data="chartData"
            :options="chartOptions"/>
          </v-card-text>
        </v-card>
      </v-card>
    </v-expansion-panel-content>
  </v-expansion-panel>
</div>
</template>

<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import navbar from '@/components/useful/NavBar.vue'
import axios from 'axios'
import { GChart } from 'vue-google-charts'
import localForage from 'localforage'
import farmhand from '@/mixins/farmhandMixin'

var deviseid
var token
var email
export default {
  name: 'Water',
  components: {
    'nav-bar': navbar,
    GChart,
    FontAwesomeIcon
  },
  data () {
    return {
      chartData: [],
      chartOptions: {},
      crops: '',
      items: [
        { title: 'Today' },
        { title: 'Week' },
        { title: 'Month' },
        { title: 'Crop Cycle' }
      ],
      feedback: 4,
      ticksLabels: ['-15', '-10', '-5', '0', '+5', '+10', '+15'],
      alert: false,
      endDate: '',
      menu: false,
      maxExtendEndDate: '',
      alert1: false
    }
  },
  methods: {
    extendEndDate (fcid, date) {
      var obj = this
      obj.menu = false
      console.log(fcid)
      console.log(date)
      if (date) {
        axios({
          method: 'post',
          url: obj.$hostname + '/fieldcrop/extendenddate',
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          },
          data: {
            'fcid': fcid,
            'date': date
          }
        }).then(response => {
          obj.alert1 = true
          console.log(response)
          // obj.onPageLoad()
          obj.$router.go(0)
        }).catch(error => {
          console.log(error)
        })
      }
    },
    pushFeedback (i, cropfeedback) {
      var fc = this.crops[i].fieldCropId
      var obj = this
      var date = Date()
      axios({
        method: 'post',
        url: obj.$hostname + '/feedback/create',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'percentChange': cropfeedback,
          'timeStamp': date,
          'fieldCropId': fc
        }
      }).then(response => {
        console.log(response)
        obj.alert = true
        // obj.onPageLoad()
      }).catch(error => {
        console.log(error)
      })
    },
    chart (event) {
      if (event === 'Today') {
        this.chartData = [
          ['Hour', 'Planned', 'Delivered'],
          ['07:00', 10, 20],
          ['12:00', 15, 25],
          ['17:00', 10, 10]
        ]
        this.chartOptions = {
          title: 'Today',
          legend: { position: 'bottom' }
        }
      } else if (event === 'Week') {
        this.chartData = [
          ['Days', 'Planned', 'Delivered'],
          ['M', 35, 55],
          ['T', 40, 60],
          ['W', 35, 35],
          ['T', 30, 25],
          ['F', 40, 35],
          ['S', 35, 30],
          ['S', 40, 45]
        ]
        this.chartOptions = {
          title: 'Last 7 Days',
          legend: { position: 'bottom' }
        }
      } else if (event === 'Month') {
        this.chartData = [
          ['Days', 'Planned', 'Delivered'],
          ['1 -5', 150, 250],
          ['6-11', 200, 300],
          ['12-17', 150, 150],
          ['18-23', 150, 125],
          ['24-30', 200, 180]
        ]
        this.chartOptions = {
          title: 'Last 30 Days',
          legend: { position: 'bottom' }
        }
      } else if (event === 'Crop Cycle') {
        this.chartData = [
          ['Hour', 'Planned', 'Delivered'],
          ['1 -5', 150, 250],
          ['6-11', 200, 300],
          ['12-17', 150, 150],
          ['18-23', 150, 125],
          ['24-30', 200, 180]
        ]
        this.chartOptions = {
          title: 'Crop Cycle',
          legend: { position: 'bottom' }
        }
      }
    },
    onPageLoad () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        deviseid = value.devise_id
        token = value.token
        email = value.email
        axios({
          method: 'get',
          url: obj.$hostname + '/feedback/all?farmId=' + localStorage.getItem('farm'),
          headers: {
            deviseid: deviseid,
            token: token,
            email: email
          }
        }).then(response => {
          obj.crops = response.data.data
          var i
          var cLength = obj.crops.length
          for (i = 0; i < cLength; i++) {
            // obj.crops[i].feedback = 3
            var newdate = new Date(obj.crops[i].endDate)
            var date = obj.dateChange(newdate, 30)
            obj.crops[i].extendEndDate = date
            obj.crops[i].endDate2 = ''
          }
          console.log(obj.crops)
          obj.changeFarm()
        }).catch(error => {
          console.log(error)
        })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  mixins: [farmhand],
  mounted () {
    this.chart('Today')
    this.onPageLoad()
  }
}
</script>
<style>
.submitField {
    border-radius: 15px;
    border: 2px solid #8c95a3;
}
</style>



