<template>
  <div>
    <v-container>
      <phase-indicator></phase-indicator>
      <v-layout class="text-xs-left">
        <v-flex>
          <v-flex>Notification</v-flex>
          <v-flex></v-flex>
        </v-flex>
      </v-layout>
      <br><br>
      <v-layout class="text-xs-left">
        <v-flex xs3>Savings</v-flex>
        <v-flex xs9>I have saved {{waterPercentage()}} of water.</v-flex>
        <v-flex><v-btn>Share</v-btn></v-flex>
      </v-layout>
      <hr>
      <v-layout v-for="(notification, index) in allNotification" :key="index.id">
        <v-flex class="text-xs-left">
          {{ notification.matter }}
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove(index, 0)" />
        </v-flex>
      </v-layout>
      <v-layout v-for="(notification, index) in allNotification1" :key="index.id">
        <v-flex class="text-xs-left">
          No Pump/Valves connected to <b>Farm</b> : {{ notification.farmName }}, <b>Field</b> : {{ notification.fieldName }}
        </v-flex>
        <v-flex class="text-xs-right">
          <font-awesome-icon icon="times" v-on:click="remove(index, 1)" />
        </v-flex>
      </v-layout>
      <hr>
      <v-layout class="text-xs-left">
        <v-flex xs12>Alert</v-flex>
      </v-layout>
      <v-layout class="text-xs-left" v-for="alert in allAlert" :key="alert.id">
        <v-flex xs1>
          <font-awesome-icon icon="exclamation-circle" />
        </v-flex>
        <v-flex>PLEASE CHECK !<br>
          {{ alert.matter }}
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import navbar from '@/components/useful/NavBar.vue'
import farmhand from '../mixins/farmhandMixin'
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import axios from 'axios'
import ECPIndicator from './useful/ElectricityPhaseIndicator'
import localForage from 'localforage'

export default {
  name: 'HomePage',
  components: {
    FontAwesomeIcon,
    'phase-indicator': ECPIndicator,
    'nav-bar': navbar
  },
  data () {
    return {
      allNotification: [],
      allNotification1: [],
      allAlert: []
    }
  },
  methods: {
    getNotification () {
      var obj = this
      localForage.getItem('user').then(function (value) {
        axios({
          method: 'get',
          url: obj.$hostname + '/fieldactuator/info',
          headers: {
            deviseid: value.devise_id,
            token: value.token,
            email: value.email
          }
        }).then(response => {
          console.log(response.data.fieldactuator)
          obj.allNotification = [{'id': 1, 'time': '7:30 pm', 'matter': 'Irrigation period has ended', 'duration': '1hr', 'crop-name': 'crop-name', 'field': 'field'},
            {'id': 2, 'time': '8:30 pm', 'matter': 'Water delivered', 'duration': '10min', 'crop-name': 'crop-name', 'field': 'field'}]
          var i
          for (i = 0; i < response.data.fieldactuator.length; i++) {
            obj.allNotification1.push(response.data.fieldactuator[i])
          }
          obj.getOfflineNotification()
        }).catch(error => {
          console.log(error)
        })
      }).catch(error => {
        console.log(error)
      })
    },
    getOfflineNotification () {
      localForage.setItem('notification', this.allNotification)
      localForage.setItem('notification1', this.allNotification1)
    },
    getAlert () {
      axios({
        method: 'get',
        url: '',
        params: { }
      }).then(response => {
        this.allAlert = [{'id': 1, 'time': '7:30 pm', 'matter': 'Irrigation period has ended', 'duration': '1hr', 'crop-name': 'crop-name', 'field': 'field'},
          {'id': 2, 'time': '8:30 pm', 'matter': 'Water delivered', 'duration': '10min', 'crop-name': 'crop-name', 'field': 'field'}]
        this.getOfflineAlert()
      }).catch(error => {
        console.log(error)
        var obj = this
        localForage.getItem('alert').then(function (value) {
          obj.allAlert = value
        }).catch(function (err) {
          console.log(err)
        })
      })
    },
    getOfflineAlert () {
      localForage.setItem('alert', this.allAlert)
    },
    waterPercentage () {
      return '35 ltr'
    },
    remove (index, n) {
      if (n === 0) {
        this.allNotification.splice(index, 1)
      } else {
        this.allNotification1.splice(index, 1)
      }
    }
  },
  mixins: [farmhand],
  created () {
    this.token()
    this.getAlert()
    this.getNotification()
  }
}
</script>



