<template>
  <div >
    <v-dialog v-model="farmdialog" persistent max-width="290">
      <v-card>
        <v-card-title class="headline">Farm details</v-card-title>
        <v-card-text>You have selected <b>{{farmName}}</b> farm.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" flat @click="farmAgree">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-navigation-drawer
      v-model="drawer"
      fixed
      clipped
      color="#5d5d5d"
    >
    <v-subheader class="mt-3 grey--text text--darken-1">Select Farm</v-subheader>
    <hr>
      <v-list dense>
        <v-list-tile v-for="item in items" :key="item.farmName" @click="selectFarm(item.farmId, item.farmName)">
          <v-list-tile-action>
            <!-- <v-icon>{{ item.icon }}</v-icon> -->
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              <span v-if="item.farmId === farmId">
                <span style="color:Green">{{ item.farmName }}</span>
              </span>
              <span v-else>
                <span>{{ item.farmName }}</span>
              </span>
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-subheader class="mt-3 grey--text text--darken-1">Profile</v-subheader>
        <hr>
        <v-list>
          <v-list-tile v-for="menu in menus" :key="menu.text" avatar @click="select(menu.text)">
            <v-list-tile-avatar>
              <!-- <img :src="`https://randomuser.me/api/portraits/men/${menu.picture}.jpg`" alt=""> -->
            </v-list-tile-avatar>
            <v-list-tile-title>
              <span v-if="module === menu.text">
                <span style="color:#3985ce">{{menu.text}}</span>
              </span>
              <span v-else>
                <span>{{menu.text}}</span>
              </span>
            </v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-list>
      <img src="@/assets/logo.png" height=100 @click="home"><br>
      <span>Version 1.0.0</span>
    </v-navigation-drawer>
    <v-toolbar
      color="white"
      dense
      fixed
      clipped-left
      app
      height=60
    >
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-icon class="mx-3">fab fa-youtube</v-icon>
      <!-- <v-toolbar-title class="align-center">
        <span class="title">Farmhand</span>
      </v-toolbar-title> -->
      <v-spacer></v-spacer>
      <v-layout row align-center style="max-width: 650px">
        <v-flex id="flex" class="text-xs-left">
          <img class="mx-auto" src="@/assets/logo2.png" alt="logo" id="logo" @click="home">
        </v-flex>
      </v-layout>
      <div class="text-xs-center mt-2" id="contact">
        <span @click="contact" id="iconcon" :style="color"><u>Contact</u></span>
        <font-awesome-icon class="ml-2 pt-2" :color="icolor" icon="cog" id="iconset" @click="setting"/>
      </div>
    </v-toolbar>
    <v-bottom-nav id="nav" class="mt-1" :value="true" :active.sync="e1" absolute  height="100px">
      <v-btn flat @click="clickPump">
        <span :style="colorBtnP">
          <img src="@/assets/icons8-Wpump-filled-50.png" alt="pump logo" width="36px" v-show='!showIcon'>
          <img src="@/assets/icons8-pump-filled-50.png" alt="pump logo" width="36px" v-show='showIcon'>
          <br><br>Pump
        </span>
      </v-btn>
      <v-btn flat @click="clickWater">
        <span :style="colorBtnW"><font-awesome-icon icon="tint" id="icon"/><br><br>Water</span>
      </v-btn>
      <v-btn flat @click="clickCrop">
        <span :style="colorBtnC"><font-awesome-icon icon="seedling" id="icon"/><br><br>Crop</span>
      </v-btn>
      <v-btn flat @click="clickGrid">
        <span :style="colorBtnG"><font-awesome-icon icon="chart-bar" id="icon"/><br><br>Grid</span>
      </v-btn>
    </v-bottom-nav>
  </div>
</template>
<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import farmhand from '@/mixins/farmhandMixin'
import localForage from 'localforage'
import axios from 'axios'
export default {
  data () {
    return {
      showIcon: true,
      e1: 'recent',
      color: 'color:#5d5d5d',
      icolor: '#5d5d5d',
      colorBtnP: 'color:#5d5d5d',
      colorBtnW: 'color:#5d5d5d',
      colorBtnG: 'color:#5d5d5d',
      colorBtnC: 'color:#5d5d5d',
      imgSrc: 'src/assets/icons8-pump-filled-50.png',
      drawer: false,
      items: [],
      menus: [
        { text: 'Profile' },
        { text: 'Logout' }
      ],
      crop: [],
      cropNew: [],
      cropCurrent: [],
      cropHistory: [],
      farmId: '',
      farmName: '',
      farmdialog: false,
      module: ''
    }
  },
  components: {
    FontAwesomeIcon
  },
  methods: {
    clickPump () {
      this.module = 'pump'
      this.showIcon = false
      this.$router.push('/farm/pump')
      this.color = 'color:#5d5d5d'
      this.icolor = '#5d5d5d'
      this.colorBtnP = 'color:white'
      this.imgSrc = '/assets/icons8-Wpump-filled-50.png'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnG = 'color:#5d5d5d'
    },
    clickWater () {
      this.colorBtnW = 'color:white'
      this.showIcon = true
      this.$router.push('/farm/water')
      this.color = 'color:#5d5d5d'
      this.icolor = '#5d5d5d'
      this.imgSrc = '/assets/icons8-pump-filled-50.png'
      this.colorBtnW = 'color:white'
      this.colorBtnP = 'color:#5d5d5d'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnG = 'color:#5d5d5d'
    },
    clickCrop () {
      this.module = 'crop'
      this.showIcon = true
      this.$router.push('/farm/crop')
      this.color = 'color:#5d5d5d'
      this.icolor = '#5d5d5d'
      this.imgSrc = '/assets/icons8-pump-filled-50.png'
      this.colorBtnC = 'color:white'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnP = 'color:#5d5d5d'
      this.colorBtnG = 'color:#5d5d5d'
    },
    clickGrid () {
      this.module = 'grid'
      this.showIcon = true
      this.$router.push('/farm/grid')
      this.color = 'color:#5d5d5d'
      this.icolor = '#5d5d5d'
      this.imgSrc = '/assets/icons8-pump-filled-50.png'
      this.colorBtnG = 'color:white'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnP = 'color:#5d5d5d'
    },
    contact () {
      this.module = 'contact'
      this.showIcon = true
      this.colorBtnG = 'color:#5d5d5d'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnP = 'color:#5d5d5d'
      this.$router.push('/farm/contact')
      if (this.color === 'color:#5d5d5d') {
        this.color = 'color:#3985ce'
        this.icolor = '#5d5d5d'
      } else {
        this.color = 'color:#5d5d5d'
      }
    },
    setting () {
      this.module = 'setting'
      this.showIcon = true
      this.colorBtnG = 'color:#5d5d5d'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnP = 'color:#5d5d5d'
      this.$router.push('/farm/settings')
      if (this.icolor === '#5d5d5d') {
        this.icolor = '#3985ce'
        this.color = 'color:#5d5d5d'
      } else {
        console.log('black')
        this.icolor = '#5d5d5d'
      }
    },
    home () {
      this.module = 'home'
      this.showIcon = true
      this.$router.push('/farm/home')
      this.color = 'color:#5d5d5d'
      this.icolor = '#5d5d5d'
      this.colorBtnG = 'color:#5d5d5d'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnP = 'color:#5d5d5d'
    },
    select (event) {
      this.showIcon = true
      this.colorBtnG = 'color:#5d5d5d'
      this.colorBtnC = 'color:#5d5d5d'
      this.colorBtnW = 'color:#5d5d5d'
      this.colorBtnP = 'color:#5d5d5d'
      this.icolor = '#5d5d5d'
      this.color = 'color:#5d5d5d'
      if (event === 'Logout') {
        var obj = this
        localForage.getItem('user').then(function (value) {
          axios({
            method: 'get',
            url: obj.$hostname + '/users/logout',
            headers: {
              deviseid: value.devise_id,
              token: value.token,
              email: value.email
            }
          }).then(response => {
            console.log(response.data)
          }).catch(error => {
            console.log(error)
          })
        }).catch(function (err) {
          console.log(err)
        })
        localForage.clear()
        localStorage.clear()
        this.$router.push('/')
      }
      if (event === 'Profile') {
        this.module = 'Profile'
        this.$router.push('/farm/profile')
      }
    },
    selectFarm (farmId, farmName) {
      console.log('Farm Id : ' + farmId)
      localStorage.setItem('farm', farmId)
      localStorage.setItem('farmName', farmName)
      this.farmId = farmId
      this.farmName = farmName
      this.farmdialog = true
      this.changeFarm()
    },
    farmAgree () {
      var obj = this
      obj.farmdialog = false
      obj.$router.go(0)
    },
    onPageLoad () {
      console.log('onPageLoad')
      var obj = this
      localForage.getItem('user').then(function (value) {
        axios({
          method: 'get',
          url: obj.$hostname + '/farm/all',
          headers: {
            deviseid: value.devise_id,
            token: value.token,
            email: value.email
          }
        }).then(response => {
          obj.items = response.data.farms
          if (localStorage.getItem('farm')) {
            console.log('yes')
            obj.farmId = parseInt(localStorage.getItem('farm'))
          } else {
            console.log('no')
            localStorage.setItem('farm', response.data.farms[0].farmId)
            localStorage.setItem('farmName', response.data.farms[0].farmName)
            obj.changeFarm()
          }
        }).catch(error => {
          console.log(error)
        })
      }).catch(function (err) {
        console.log(err)
      })
    }
  },
  mixins: [farmhand],
  mounted () {
    this.token()
    this.onPageLoad()
    var hash = window.location.hash
    switch (hash) {
      case '#/farm/pump':
        this.clickPump()
        break
      case '#/farm/water':
        this.clickWater()
        break
      case '#/farm/crop':
        this.clickCrop()
        break
      case '#/farm/grid':
        this.clickGrid()
        break
      case '#/farm/home':
        this.home()
        break
      case '#/farm/contact':
        this.contact()
        break
      case '#/farm/settings':
        this.setting()
        break
      default:
        this.select('Profile')
    }
  }
}
</script>
<style scoped>
#icon{
  height: 35px;
  width: 35px;
}
#nav{
  top: -73px;
  background: linear-gradient(0.30turn, #75d3ea, #3985ce);
}
#contact{
  font-size: 18px;
}
#iconset{
  height: 25px;
  width: 25px;
  cursor: pointer;
}
#logo{
  height: 50px;
  width: 50px;
  cursor: pointer;
}
#iconcon{
  cursor: pointer;
}
.menuicon{
  font-size: 18px;
}
</style>



