<template>
  <div>
    <v-alert v-model="alert" type="success" dismissible>
      Your Data has Successfully been Submitted
    </v-alert>
    <v-container>
      <v-layout ref="forms" row wrap justify-center justify-space-around>
        <v-flex xs12 sm2 class="cropname">
          <div class="text-xs-center">
            <v-menu offset-x>
              <div slot="activator"> Crop Name: {{cropName}}</div>
              <v-list>
                <v-list-tile v-for="(name, index) in cropnames" :key="index" @click="selectCrop(name, index)">
                  <v-list-tile-title>{{ name.cropName }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </div>
        </v-flex>
        <v-flex xs12 sm2 class="mt-1 cropname">
          <div class="text-xs-center">
            <v-menu offset-x>
              <div slot="activator" >Crop Type: {{cropTypeName}}</div>
              <v-list>
                <v-list-tile v-for="(type, index) in cropTypes" :key="index" @click="selectType(type)">
                  <v-list-tile-title>{{ type.cropTypeName }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </div>
        </v-flex>
        <v-flex xs12 sm2 class="mt-1 cropname">
          <div class="text-xs-center">
            <v-menu offset-x>
              <div slot="activator" >Field No.: &nbsp {{no}}</div>
              <v-list>
                <v-list-tile v-for="(field, index) in fields" :key="index" @click="selectField(field)">
                  <v-list-tile-title>{{ field.fieldName }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </div>
        </v-flex>
        <v-flex xs12 sm2 class="mt-1 cropname">
          <v-dialog
            ref="dialogStartDate"
            v-model="modalStartDate"
            :return-value.sync="date"
            persistent
            lazy
            full-width
            width="290px"
          >
            <div slot="activator" >Start <font-awesome-icon icon="calendar-alt" id="icon"/> &nbsp {{date}}</div>
            <v-date-picker v-model="date" :min="freeFrom" color="indigo accent-3" scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="modalStartDate = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.dialogStartDate.save(date)">OK</v-btn>
            </v-date-picker>
          </v-dialog>
        </v-flex>
        <v-flex xs12 sm2 class="mt-1 cropname">
          <v-dialog
            ref="dialogEndDate"
            v-model="modalEndDate"
            :return-value.sync="extendEndDate"
            persistent
            lazy
            full-width
            width="290px"
          >
            <div slot="activator" ><span v-if="extendEndDate">Extended to: &nbsp{{extendEndDate}}</span><span v-else>End Autofill: &nbsp {{ endDate() }}</span></div>
            <!-- <v-date-picker v-model="extendEndDate" :min="someFormattedDate" :max="maxExtendEndDate" color="indigo accent-3"  scrollable> -->
            <v-date-picker v-model="extendEndDate" :min="date" color="indigo accent-3"  scrollable>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="modalEndDate = false">Cancel</v-btn>
              <v-btn flat color="primary" @click="$refs.dialogEndDate.save(extendEndDate)">OK</v-btn>
            </v-date-picker>
          </v-dialog>
        </v-flex>
        <v-flex xs12 sm2 class="mt-1 cropname">
          <div class="text-xs-center">
            <v-menu offset-x>
              <div slot="activator"> Mode : {{modeName}}</div>
              <v-list>
                <v-list-tile v-for="(name, index) in modenames" :key="index" @click="selectMode(name, index)">
                  <v-list-tile-title>{{ name.modeName }}</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
    <v-btn class="mt-5" color="info" type="submit" @click="sendData">Confirm</v-btn>
  </div>
</template>
<script>
import FontAwesomeIcon from '@fortawesome/vue-fontawesome'
import axios from 'axios'
import localForage from 'localforage'
import farmhand from '@/mixins/farmhandMixin'

var deviseid
var token
var email
export default {
  data () {
    return {
      names: '',
      cropnames: '',
      modenames: [{'irrigationMode': 1, 'modeName': 'Automated'}, {'irrigationMode': 2, 'modeName': 'Semi-automated'}, {'irrigationMode': 3, 'modeName': 'Manual'}],
      types: '',
      duration: '',
      fields: '',
      picker: null,
      cropName: '',
      modeName: 'Automated',
      irrigationMode: 1,
      cropTypeId: '',
      cropTypeName: '',
      cropId: '',
      fieldId: '',
      today: '',
      no: '',
      date: '',
      extendEndDate: '',
      maxExtendEndDate: '',
      alert: false,
      someFormattedDate: '',
      i: '',
      crops: [],
      freeFrom: '',
      days: '',
      enable: true,
      fieldcrop: [],
      cropCurrent: [],
      cropHistory: [],
      modalStartDate: false,
      modalEndDate: false,
      cropTypes: []
    }
  },
  components: {
    FontAwesomeIcon
  },
  mounted () {
    this.dateToday()
  },
  methods: {
    dateToday () {
      var newdate = new Date()
      this.today = this.dateChange(newdate, 0)
      console.log(this.today)
    },
    selectMode (crop) {
      this.modeName = crop.modeName
      this.irrigationMode = crop.irrigationMode
    },
    selectCrop (crop) {
      var i
      this.cropName = crop.cropName
      this.cropTypes = []
      for (i = 0; i < this.types.length; i++) {
        if (this.cropName === this.types[i].cropName) {
          this.cropTypes = this.types[i].cropTypes
          break
        }
      }
    },
    selectType (crop) {
      this.cropTypeName = crop.cropTypeName
      this.cropTypeId = crop.cropTypeId
      this.cropId = crop.cropId
    },
    selectField (field) {
      this.freeFrom = field.startDate
      this.no = field.fieldName
      this.fieldId = field.fieldId
      this.enable = false
    },
    reset () {
      this.cropName = ''
      this.cropTypeName = ''
      this.no = ''
      this.date = ''
      this.someFormattedDate = ''
    },
    endDate () {
      var a = 0
      var newdate
      var dr = this.duration
      var days = 0

      for (a = 0; a < dr.length; a++) {
        if (dr[a] !== null) {
          if (dr[a].cropId === this.cropId) {
            days = dr[a].endDay
            break
          }
        }
      }
      var tt = this.date
      if (tt !== '') {
        if (days === null) {
          days = 30
        }
        newdate = new Date(tt)
        this.someFormattedDate = this.dateChange(newdate, days)
        console.log(this.someFormattedDate)
        newdate = new Date(this.someFormattedDate)
        this.maxExtendEndDate = this.dateChange(newdate, 30)
        console.log(this.maxExtendEndDate)
        return this.someFormattedDate
      }
    },
    sendData () {
      var endDate = this.someFormattedDate
      var newEndDate = this.extendEndDate
      axios({
        method: 'post',
        url: this.$hostname + '/fieldcrop/create',
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        },
        data: {
          'cropId': this.cropId,
          'fieldId': this.fieldId,
          'status': 1,
          'irrigationMode': this.irrigationMode,
          'startDate': this.date,
          'endDate': endDate,
          'newEndDate': newEndDate
        }
      }).then(response => {
        this.extendEndDate = ''
        this.alert = true
        this.reset()
        this.getData()
      }).catch(error => { console.log(error) })
    },
    getData () {
      var obj = this
      axios({
        method: 'get',
        url: this.$hostname + '/crops/all?farmId=' + localStorage.getItem('farm'),
        headers: {
          deviseid: deviseid,
          token: token,
          email: email
        }
      }).then(res => {
        console.log(res.data)
        obj.names = res.data.names
        obj.cropnames = res.data.names
        // obj.types = [{'cropTypeId': 1, 'cropTypeName': 'Seed'}, {'cropTypeId': 2, 'cropTypeName': 'Sapling'}]
        obj.types = res.data.crops
        obj.duration = res.data.duration
        obj.fields = res.data.field
      }).catch(error => { console.log(error) })
    }
  },
  mixins: [farmhand],
  created () {
    var obj = this
    localForage.getItem('user').then(function (value) {
      deviseid = value.devise_id
      token = value.token
      email = value.email
      obj.getData()
    }).catch(function (err) {
      console.log(err)
    })
  }
}
</script>
<style scoped>
.cropname{
  padding: 10px;
  border: 1px solid black;
  border-radius: 25px;
}
</style>



